@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #fa3263;
  --main-color-light: #fa4a76;
  --dark-color: #171717;
  --dark-color-light: #222222;
}

html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  background-color: #171717;
  background-color: var(--dark-color);
}

.btn_round {
  background-color: #fa3263;
  background-color: var(--main-color);
}

.btn_round,
.btn_round_outline {
  border: none;
  color: #fff;
  max-width: 200px;
  width: 100%;
  min-height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 100px;
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: all 0.3s ease-in-out;
}


.btn_round_outline {
  background-color: transparent;
  border: 3px solid #fff;
}

.btn_round:hover {
  color: #ffffffe8;
  background-color: #fa4a76;
  background-color: var(--main-color-light);
  box-shadow: rgba(250, 76, 119, 0.205) 0px 4px 12px;
  border-color: #fa4a76;
  border-color: var(--main-color-light);
}

.btn_round_outline:hover {
  color: #fa3263;
  color: var(--main-color);
  border-color: #fff;
  background-color: #fff;
}

.fa.fa-star.fa-2x {
  color: #fa3263;
  color: var(--main-color);
}

.img-fluid {
  max-width: 100%;
  height: auto;
  width: 100%;
  height: 240px;
  object-fit: cover;
}

.card_item>.img-fluid {
  width: auto;
}

.fas.fa-info-circle {
  font-size: 20px;
  color: #00b6d2;
}

.status.pending {
  color: #fa3263;
}

.status.done {
  color: #59af8a;
}

.status.on-going {
  color: #ffb247;
}

/* spinner */
.spinner_container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #222222;
  background-color: var(--dark-color-light);
  z-index: 9999;
  transition: all 0.3s ease-in;
  opacity: 0;
  pointer-events: none;
}

.spinner_container.active {
  opacity: 1;
  pointer-events: all;
}

.swiper-pagination-bullet {
  width: 16px !important;
  height: 16px !important;
}

.spinner_wrapper {
  width: 200px;
  height: 200px;
}



@keyframes animation {
  0% {
    stroke-dasharray: 1 98;
    stroke-dashoffset: -105;
  }

  50% {
    stroke-dasharray: 80 10;
    stroke-dashoffset: -160;
  }

  100% {
    stroke-dasharray: 1 98;
    stroke-dashoffset: -300;
  }
}

#spinner {
  transform-origin: center;
  animation-name: animation;
  animation-duration: 1.2s;
  animation-timing-function: cubic-bezier;
  animation-iteration-count: infinite;
}

.input_item {
  display: flex;
  flex-direction: column;
}

.input_item>select,
.input_item>input {
  height: 40px;
  outline: none;
  border: none;
  margin-bottom: 15px;
  border-radius: 5px;
  background-color: var(--dark-color-light);
  color: #ffffff;
  transition: all 0.3s ease-in-out;
}

.input_item>select:focus,
.input_item>input:focus,
.input_item>textarea:focus {
  box-shadow: 0px 0px 0px 3px rgb(250, 51, 101);
}

.input_item>input:-ms-input-placeholder {
  color: #ffffffb4;
}

.input_item>input::placeholder {
  color: #ffffffb4;
}

.input_item>textarea {
  outline: none;
  border: none;
  border-radius: 5px;
  background-color: var(--dark-color-light);
  color: #ffffff;
  padding: 10px;
}

.input_item>label {
