  }
}

@media screen and (max-width: 992px) {
  .navbar_container {
    height: 70px;
  }

  .nav_items.mobile_menu {
    display: block;
  }


  .nav_menu {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--main-color);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-left: 0;
    transition: all 0.3s ease;
    overflow: hidden;
    z-index: 99;
  }

  .humberger_close {
    position: absolute;
    top: 20px;
    right: 30px;
  }

  .humberger_close i,
  .humberger_nav_icon i {
    display: block;
  }

  .humberger_close i {
    font-size: 30px;
  }

  .nav_menu.active {
    margin-right: 100%;
  }

  .dashboard_btn_link {
    display: inline;
  }

  .nav_menu .nav_items {
    margin: 15px 0;
  }

  .nav_menu .nav_items .nav_link {
    font-size: 20px;
  }

  .connect {
    display: none;
  }

  .nav_link.active,
  .link_active {
    color: var(--dark-color) !important;
    font-weight: 800;
  }
}

