}

.service_header p {
  color: #e9e9e9;
}

@media screen and (max-width: 778px) {
  .service_header p {
    font-size: 14px !important;
  }
}

.form_wrapper {
  margin: 50px auto;
}

.form_item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.border_right {
  border-right: 3px solid var(--light-grey-color);
}

.form_item.sign {
  flex-direction: column;
}

.form_item.sign .input_field {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.form_item.sign img {
  margin-right: 10px;
  width: 20px;
  height: auto;
}

.form {
  margin: 15px 0;
  width: 100%;
}

.form form {
  display: flex;
  flex-direction: column;
  background-color: #f6f6f6;
  border-radius: 24px;
  background-color: var(--dark-color-light);
  padding: 48px 50px;
  margin: 50px;
}


.input_field,
.input_btn {
  width: 100%;
  border-radius: 24px;
  background-color: #fff;
  padding: 12px 24px;
  outline: none;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  border: none;
}

.form_item.sign>.input_field:nth-child(1) {
  background-color: #f6f6f6;
  color: var(--dark-color);
}

.form_item.sign>.input_field:nth-child(2) {
  background-color: #32dd96;
  color: #ffffff;
}

.form_item.sign>.input_field:nth-child(3) {
  background-color: #3985e4;
  color: #ffffff;
}

.forgot {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.forgot a {
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  line-height: 1.43;
  color: var(--main-color);
}

.forgot input {
  margin-right: 5px;
}

.input_btn {
  background-color: #ec927e !important;
  color: #ffffff !important;
}

.form_header {
  color: #fff;
  margin-left: 50px;
}

@media screen and (max-width: 778px) {
  .border_right {
    border-right: none;
  }

  .form>form {
    margin: 0;
    padding: 30px 20px;
  }

  .form_item.sign .input_field {
    font-size: 14px;
    padding: 14px 5px;
  }

  .flex_column_reverse {
    flex-direction: column-reverse !important;
  }
}

.not_found {
  background-color: var(--dark-color);
  height: 100vh;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.not_found h2 {
  color: #fff;
  font-size: 32px;
  font-weight: 600;
}

.not_found p {
  font-size: 14px;
  color: #ddd;
}

.not_found a {
  font-size: 14px;
  font-weight: 600;
  color: var(--main-color);
  text-decoration: none;
}


.contact-form {
  text-align: center;
}

.form-group {
  margin-bottom: 15px;
}

.contact-form form .form-control {
  background-color: var(--dark-color);
  border: none;
  height: 60px;
  border: 1px solid var(--main-color);
  transition: .5s;
  font-size: 16px;
}

.form-control {
  display: block;
  width: 100%;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  appearance: none;
  border-radius: .25rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.form-group>input {
  color: #fff;
}

.form-group>textarea {
  background: (--dark-color-light) !important;
}

.form-group>textarea:focus,
.form-group>input:focus {
  box-shadow: rgba(250, 50, 100, 0.6) 0px 0px 0px 3px;
  color: #fff;
}

.form-group>input:-ms-input-placeholder {
  color: #ffffffaf;
}

.form-group>input::placeholder {
  color: #ffffffaf;
}

.contact-form form textarea.form-control {
  height: auto;
  padding-top: 15px;
}

.contact-form form .form-control {
  background-color: var(--dark-color-light) !important;
  border: none;
  height: 60px;
  border: 1px solid var(--dark-color);
  transition: .5s;
  font-size: 16px;
}

